*, :before, :after {
  box-sizing: border-box;
}

:root {
  --bg: #f5f6f8;
  --text: #111827;
  --muted: #6b7280;
  --border: #e5e7eb;
  --accent: #3b82f6;
  --radius: 12px;
  --max-width: 920px;
}

body {
  background: var(--bg);
  color: var(--text);
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  line-height: 1.5;
}

body > * {
  width: min(var(--max-width), 92vw);
  margin-left: auto;
  margin-right: auto;
}

.breed-select {
  width: 340px;
  height: 44px;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  cursor: pointer;
  background: #fff;
  margin: 24px auto 0;
  padding: 0 14px;
  display: block;
}

.breed-select:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, .15);
}

.loader {
  border: 1px solid var(--border);
  border-radius: var(--radius);
  color: var(--muted);
  background: #fff;
  align-items: center;
  gap: 10px;
  margin-top: 12px;
  padding: 10px 12px;
  display: none;
}

.loader:before {
  content: "";
  width: 16px;
  height: 16px;
  border: 2px solid rgba(0, 0, 0, .15);
  border-top-color: var(--accent);
  border-radius: 50%;
  animation: spin .8s linear infinite;
}

.loader.is-loading {
  display: flex;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.cat-info {
  max-width: 600px;
  margin: 20px auto 40px;
  padding: 16px;
}

.cat-card {
  width: 100%;
  max-width: 600px;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  background: #fff;
  overflow: hidden;
}

.cat-card__img {
  width: 100%;
  height: auto;
  max-height: 360px;
  object-fit: contain;
  background: #fff;
  display: block;
}

.cat-card__content {
  flex: 1;
  padding: 14px 16px;
  overflow: auto;
}

.cat-card__title {
  margin: 0 0 8px;
  font-size: 20px;
  font-weight: 600;
}

.cat-card__description, .cat-card__temperament {
  color: var(--muted);
  margin: 0 0 10px;
  font-size: 14px;
  line-height: 1.6;
}

.cat-card__temperament strong {
  color: var(--text);
}

.is-hidden {
  display: none;
}

@media (max-width: 560px) {
  .cat-card {
    height: 460px;
  }

  .cat-card__img {
    height: 260px;
  }

  .breed-select {
    width: 92vw;
  }
}

